h1 {
  margin-top: 0;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 2.375rem;
    font-weight: 700;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 1.5625rem;
  max-width: 50rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.signup-form {
  background-color: white;
  color: var(--dark-slate-grey);
  margin: 11.25rem auto 0;
}
@media (min-width: 40em) {
  .signup-form {
    margin: 11.25rem auto 0;
    padding: 1.25rem;
    background-color: white;
  }
}